import React, { useState } from 'react';
import ReactDOM from 'react-dom';
import   './index.css';

var link_img1="https://source.unsplash.com/1600x900/?";
function Images(props){

    return (
    <>
        <div className="col-md-3 col-5 my-4 p-3 border" id="div">
           <img src={`${props.srcs}`} alt="" className="img-fluid border-dark hover"/>
           <h1 className='lead text-uppercase text-light my-3'>{props.title}</h1>
           <a href={props.link}>
           <button className="btn btn-outline-danger text-warning">Watch Now</button>
           </a>
        </div>
    </>
    );
}

export default Images;